* {
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  color: #615551;
  min-height: calc(100vh - 8vw);
  background-image: linear-gradient(to bottom right, #fbdb89, #f48982);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Nunito Sans, sans-serif;
  font-weight: 400;
  line-height: 1.6;
}

.container {
  max-width: 135rem;
  min-height: 117rem;
  background-color: #fff;
  border-radius: 9px;
  grid-template: "head head" 10rem
                 "list recipe" minmax(100rem, auto)
                 / 1fr 2fr;
  margin: 4vw auto;
  display: grid;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2rem 6rem .5rem #61555133;
}

.btn--small, .btn--small:link, .btn--small:visited, .btn {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-image: linear-gradient(to bottom right, #fbdb89, #f48982);
  border: none;
  border-radius: 10rem;
  align-items: center;
  transition: all .2s;
  display: flex;
}

.btn--small:hover, .btn:hover {
  transform: scale(1.05);
}

.btn--small:focus, .btn:focus {
  outline: none;
}

.btn--small > :first-child, .btn > :first-child {
  margin-right: 1rem;
}

.btn {
  padding: 1.5rem 4rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.btn svg {
  height: 2.25rem;
  width: 2.25rem;
  fill: currentColor;
}

.btn--small, .btn--small:link, .btn--small:visited {
  padding: 1.25rem 2.25rem;
  font-size: 1.4rem;
  font-weight: 600;
  text-decoration: none;
}

.btn--small svg, .btn--small:link svg, .btn--small:visited svg {
  height: 1.75rem;
  width: 1.75rem;
  fill: currentColor;
}

.btn--inline {
  color: #f38e82;
  cursor: pointer;
  background-color: #f9f5f3;
  border: none;
  border-radius: 10rem;
  align-items: center;
  padding: .8rem 1.2rem;
  font-size: 1.3rem;
  font-weight: 600;
  transition: all .2s;
  display: flex;
}

.btn--inline svg {
  height: 1.6rem;
  width: 1.6rem;
  fill: currentColor;
  margin: 0 .2rem;
}

.btn--inline span {
  margin: 0 .4rem;
}

.btn--inline:hover {
  color: #f48982;
  background-color: #f2efee;
}

.btn--inline:focus {
  outline: none;
}

.btn--round {
  cursor: pointer;
  height: 4.5rem;
  width: 4.5rem;
  background-image: linear-gradient(to bottom right, #fbdb89, #f48982);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .2s;
  display: flex;
}

.btn--round:hover {
  transform: scale(1.07);
}

.btn--round:focus {
  outline: none;
}

.btn--round svg {
  height: 2.5rem;
  width: 2.5rem;
  fill: #fff;
}

.btn--tiny {
  height: 2rem;
  width: 2rem;
  cursor: pointer;
  background: none;
  border: none;
}

.btn--tiny svg {
  height: 100%;
  width: 100%;
  fill: #f38e82;
  transition: all .3s;
}

.btn--tiny:focus {
  outline: none;
}

.btn--tiny:hover svg {
  fill: #f48982;
  transform: translateY(-1px);
}

.btn--tiny:active svg {
  fill: #f48982;
  transform: translateY(0);
}

.btn--tiny:not(:last-child) {
  margin-right: .3rem;
}

.heading--2 {
  color: #f38e82;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 2.5rem;
  font-size: 2rem;
  font-weight: 700;
}

.link:link, .link:visited {
  color: #918581;
}

.spinner {
  text-align: center;
  margin: 5rem auto;
}

.spinner svg {
  height: 6rem;
  width: 6rem;
  fill: #f38e82;
  animation: rotate 2s linear infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.message, .error {
  max-width: 40rem;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 5rem 4rem;
  display: flex;
}

.message svg, .error svg {
  height: 3rem;
  width: 3rem;
  fill: #f38e82;
  transform: translateY(-.3rem);
}

.message p, .error p {
  margin-left: 1.5rem;
  font-size: 1.8rem;
  font-weight: 600;
  line-height: 1.5;
}

.hidden {
  visibility: hidden;
  opacity: 0;
}

.hide {
  display: none;
}

.header {
  background-color: #f9f5f3;
  grid-area: head;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  height: 4.6rem;
  margin-left: 4rem;
  display: block;
}

.search {
  background-color: #fff;
  border-radius: 10rem;
  align-items: center;
  padding-left: 3rem;
  transition: all .3s;
  display: flex;
}

.search:focus-within {
  transform: translateY(-2px);
  box-shadow: 0 .7rem 3rem #61555114;
}

.search__field {
  color: inherit;
  width: 30rem;
  background: none;
  border: none;
  font-family: inherit;
  font-size: 1.7rem;
}

.search__field:focus {
  outline: none;
}

.search__field::placeholder {
  color: #d3c7c3;
}

.search__btn {
  font-family: inherit;
  font-weight: 600;
}

.nav {
  align-self: stretch;
  margin-right: 2.5rem;
}

.nav__list {
  height: 100%;
  list-style: none;
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__btn {
  height: 100%;
  color: inherit;
  text-transform: uppercase;
  cursor: pointer;
  background: none;
  border: none;
  align-items: center;
  padding: 0 1.5rem;
  font-family: inherit;
  font-size: 1.4rem;
  font-weight: 700;
  transition: all .3s;
  display: flex;
}

.nav__btn svg {
  height: 2.4rem;
  width: 2.4rem;
  fill: #f38e82;
  margin-right: .7rem;
  transform: translateY(-1px);
}

.nav__btn:focus {
  outline: none;
}

.nav__btn:hover {
  background-color: #f2efee;
}

.bookmarks {
  z-index: 10;
  width: 40rem;
  visibility: hidden;
  opacity: 0;
  background-color: #fff;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  padding: .2rem 0;
  transition: all .5s .2s;
  position: absolute;
  right: -2.5rem;
  box-shadow: 0 .8rem 5rem 2rem #6155511a;
}

.bookmarks__list {
  list-style: none;
}

.bookmarks__field {
  cursor: pointer;
  height: 100%;
  align-items: center;
  padding: 0 4rem;
  transition: all .3s;
  display: flex;
}

.bookmarks__field:hover {
  background-color: #f2efee;
}

.bookmarks:hover, .nav__btn--bookmarks:hover + .bookmarks {
  visibility: visible;
  opacity: 1;
}

.preview {
  border-right: 1px solid #fff;
  border-radius: 3px;
}

.preview:hover {
  background-color: #f9f5f3;
  transform: translateY(-2px);
}

.preview__link:link, .preview__link:visited {
  align-items: center;
  padding: 1.3rem 3.25rem;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.preview__link--active {
  background-color: #f9f5f3;
}

.preview__fig {
  height: 5.8rem;
  backface-visibility: hidden;
  border-radius: 50%;
  flex: 0 0 5.8rem;
  margin-right: 2rem;
  position: relative;
  overflow: hidden;
}

.preview__fig:before {
  content: "";
  height: 100%;
  width: 100%;
  opacity: .4;
  background-image: linear-gradient(to bottom right, #fbdb89, #f48982);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.preview__fig img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all .3s;
  display: block;
}

.preview__data {
  width: 100%;
  grid-template-columns: 1fr 2rem;
  align-items: center;
  row-gap: .1rem;
  display: grid;
}

.preview__title {
  color: #f38e82;
  text-transform: uppercase;
  text-overflow: ellipsis;
  max-width: 25rem;
  white-space: nowrap;
  font-size: 1.45rem;
  font-weight: 600;
  overflow: hidden;
}

.preview__publisher {
  color: #918581;
  text-transform: uppercase;
  grid-row: 2 / 3;
  font-size: 1.15rem;
  font-weight: 600;
}

.preview__user-generated {
  height: 2rem;
  width: 2rem;
  background-color: #eeeae8;
  border-radius: 10rem;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: 1.75rem;
  display: flex;
}

.preview__user-generated svg {
  height: 1.2rem;
  width: 1.2rem;
  fill: #f38e82;
}

.preview__day-select {
  background-color: #f9f5f3;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 1rem;
  display: flex;
}

.preview__day-select-wrapper {
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  background-color: #eeeae8;
  border: 0;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.preview__day-select-wrapper span {
  color: #f38e82;
  font-size: 1.3rem;
  font-weight: 600;
}

.preview__day-select-wrapper:hover {
  background-color: #fff;
}

.preview__day-select-wrapper:hover span {
  color: #fbdb89;
}

.preview__day-select .active {
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  background-color: #f48982;
  border: 0;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.preview__day-select .active span {
  color: #fff;
}

.preview__day-select .active:hover {
  background-color: #fff;
  border: 1px solid #f48982;
}

.preview__day-select .active:hover span {
  color: #fbdb89;
}

.hidden {
  display: none;
}

.results .preview:nth-child(3n-1) {
  border-left: 5px solid #fbdb89;
}

.results .preview:nth-child(3n-2) {
  border-left: 5px solid #f38e82;
}

.results .preview:nth-child(3n-3) {
  border-left: 5px solid;
}

.search-results {
  max-width: 45rem;
  visibility: visible;
  opacity: 1;
  flex-direction: column;
  display: flex;
}

.sort-exit-container {
  justify-content: end;
  display: flex;
}

.btn--close-search-results {
  color: inherit;
  cursor: pointer;
  background: none;
  border: none;
  font-family: inherit;
  font-size: 4rem;
  display: none;
}

.sort__icon__container {
  justify-content: flex-end;
  align-items: center;
  padding: 1rem .5rem .5rem 0;
  display: flex;
}

.sort__icon__container .sort__icon:hover {
  cursor: pointer;
  transition: all .2s;
  transform: scale(1.1);
}

.sort__form {
  transition: all .2s linear;
}

.form__input--sort {
  color: #fff;
  background-image: linear-gradient(to bottom right, #fbdb89, #f48982);
  border: none;
  border-radius: 6px;
  outline: none;
  padding: .3rem 1.1rem;
  font-size: 1.4rem;
}

.form__input--sort option {
  background-color: #55585a;
}

.results {
  margin-bottom: 2rem;
  list-style: none;
}

.pagination {
  flex-flow: wrap;
  justify-content: space-between;
  margin-top: auto;
  padding: 0 3.5rem;
  display: flex;
}

.pagination__link:link .pagination__link:visited .pagination__link:hover {
  background-color: #f9f5f3;
  transform: translateY(-2px);
}

.pagination__link--active, .pagination__link--active:hover {
  color: #fff;
  background-color: #f48982;
}

.copyright {
  color: #918581;
  margin-top: 4rem;
  padding: 0 3.5rem 1rem;
  font-size: 1.2rem;
}

.copyright .twitter-link:link, .copyright .twitter-link:visited {
  color: #918581;
}

.ingredients {
  max-width: 40rem;
  z-index: 12;
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  background-color: #0002;
  border-bottom-left-radius: 9px;
  flex-direction: column;
  align-items: center;
  transition: all .5s;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.ingredients svg {
  height: 2.4rem;
  width: 2.4rem;
  cursor: pointer;
}

.ingredients svg:hover {
  filter: brightness(0) saturate() invert(15%) sepia(61%) saturate(5029%) hue-rotate(354deg) brightness(111%) contrast(133%);
}

.ingredients .close-list__container {
  align-self: flex-start;
}

.ingredients__list--heading {
  justify-content: center;
  align-items: flex-end;
  margin: 2rem 0 3rem;
  display: flex;
}

.ingredients__list--heading svg {
  margin-bottom: .3rem;
  margin-right: .3rem;
}

.ingredients__list--heading svg:hover {
  filter: none;
  cursor: default;
}

.ingredients .heading-2 {
  color: #f38e82;
  text-transform: uppercase;
  text-align: center;
  -webkit-text-stroke-width: .05rem;
  -webkit-text-stroke-color: black;
  font-family: cursive;
  font-size: x-large;
  font-weight: 700;
  transform: skewY(-3deg);
}

.ingredients ul {
  width: 40rem;
  height: 27.5rem;
  min-height: 0;
  max-height: calc(100vh - 8vw);
  resize: vertical;
  overflow-y: auto;
}

.ingredients li {
  box-sizing: border-box;
  border-bottom: 1px solid #f2efee;
  justify-content: space-between;
  align-items: center;
  padding: .5rem 1rem .5rem 0;
  display: flex;
}

.ingredients .ingredients__description {
  font-family: cursive;
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.5;
}

.ingredients .ingredients__description--container {
  display: flex;
}

.ingredients .ingredients__description--container svg:hover {
  filter: none;
  cursor: default;
}

.ingredients .remove__ingredient {
  height: 2.4rem;
  width: 2.4rem;
}

.ingredients .delete__all--icon {
  height: 4rem;
  width: 4rem;
  margin: 1rem 0;
}

#slide-in {
  animation: slide-in .5s forwards;
}

#slide-out {
  animation: slide-out .5s forwards;
}

@keyframes slide-in {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0%);
  }
}

@-webkit-keyframes slide-in {
  0% {
    -webkit-transform: translateX(100%);
  }

  100% {
    -webkit-transform: translateX(0%);
  }
}

@keyframes slide-out {
  0% {
    transform: translateX(0%);
  }

  100% {
    transform: translateX(100%);
  }
}

@-webkit-keyframes slide-out {
  0% {
    -webkit-transform: translateX(0%);
  }

  100% {
    -webkit-transform: translateX(100%);
  }
}

.recipe {
  background-color: #f9f5f3;
  position: relative;
  overflow-y: auto;
}

.recipe .menu__btn {
  cursor: pointer;
  width: 2.4rem;
  height: 2.4rem;
  z-index: 50;
  box-sizing: content-box;
  background-color: #918581;
  border-radius: 50%;
  padding: .5rem;
  display: none;
  position: absolute;
  top: 3rem;
  left: 3rem;
}

.recipe__view {
  width: 100%;
  height: 100%;
}

.recipe__fig {
  height: 32rem;
  transform-origin: top;
  position: relative;
}

.recipe__fig:before {
  content: "";
  height: 100%;
  width: 100%;
  opacity: .6;
  background-image: linear-gradient(to bottom right, #fbdb89, #f48982);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.recipe__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.recipe__title {
  color: #fff;
  text-transform: uppercase;
  width: 50%;
  text-align: center;
  font-size: 3.25rem;
  font-weight: 700;
  line-height: 1.95;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 20%)skewY(-6deg);
}

.recipe__title span {
  -webkit-box-decoration-break: clone;
  box-decoration-break: clone;
  background-image: linear-gradient(to bottom right, #fbdb89, #f48982);
  padding: 1.3rem 2rem;
}

@media only screen and (max-width: 900px) {
  .recipe__title {
    width: 70%;
  }
}

.recipe__details {
  align-items: center;
  padding: 7.5rem 8rem 3.5rem;
  display: flex;
}

.recipe__info {
  text-transform: uppercase;
  align-items: center;
  font-size: 1.65rem;
  display: flex;
}

.recipe__info:not(:last-child) {
  margin-right: 4.5rem;
}

.recipe__info-icon {
  height: 2.35rem;
  width: 2.35rem;
  fill: #f38e82;
  margin-right: 1.15rem;
}

.recipe__info-data {
  margin-right: .5rem;
  font-weight: 700;
}

.recipe__info-buttons {
  margin-left: 1.6rem;
  display: flex;
  transform: translateY(-1px);
}

.recipe__user-generated {
  height: 4rem;
  width: 4rem;
  background-color: #eeeae8;
  border-radius: 10rem;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: 1.75rem;
  display: flex;
}

.recipe__user-generated svg {
  height: 2.25rem;
  width: 2.25rem;
  fill: #f38e82;
}

.recipe .btn--ingredients {
  margin-right: 1.75rem;
}

.recipe .btn--ingredients:active {
  transform: scale(.98);
  box-shadow: 3px 2px 22px 1px #0000003d;
}

.recipe__ingredients {
  background-color: #f2efee;
  flex-direction: column;
  align-items: center;
  padding: 5rem 8rem;
  font-size: 1.6rem;
  line-height: 1.4;
  display: flex;
}

.recipe__ingredient-list {
  grid-template-columns: 1fr 1fr;
  gap: 2.5rem 3rem;
  margin-bottom: 4rem;
  list-style: none;
  display: grid;
}

.recipe__ingredient {
  display: flex;
}

.recipe__icon {
  height: 2rem;
  width: 2rem;
  fill: #f38e82;
  flex: none;
  margin-top: .1rem;
  margin-right: 1.1rem;
}

.recipe__quantity {
  flex: none;
  margin-right: .5rem;
}

.recipe .spin {
  animation: rotate 2s linear infinite;
}

.recipe .nutrition, .recipe .nutrition-data {
  width: 100%;
}

.recipe .nutrition-data p {
  justify-content: flex-end;
  font-size: 1rem;
  display: flex;
}

.recipe .nutrition-data ul {
  padding-left: 2rem;
}

.recipe .nutrition-data li {
  border-bottom: 1px solid #fff;
  justify-content: space-between;
  margin-top: 1rem;
  display: flex;
}

.recipe__directions {
  flex-direction: column;
  align-items: center;
  padding: 5rem 10rem;
  display: flex;
}

.recipe__directions-text {
  text-align: center;
  color: #918581;
  margin-bottom: 3.5rem;
  font-size: 1.7rem;
}

.recipe__publisher {
  font-weight: 700;
}

.overflow {
  overflow: hidden;
}

.meal-plan {
  height: 100%;
  width: 100%;
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
}

.meal-plan__heading {
  z-index: 1000;
  align-items: center;
  transition: all .5s;
  display: flex;
  position: fixed;
  top: 5%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.meal-plan__heading__icon {
  width: 5rem;
  height: 5rem;
}

.meal-plan__heading h1 {
  color: #fff;
  margin: 0 1rem;
  font-family: cursive;
  font-size: x-large;
  font-weight: 700;
}

.meal-plan-window {
  width: 140rem;
  z-index: 1000;
  box-sizing: border-box;
  justify-content: space-around;
  transition: all .5s;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.meal-plan-window-week {
  width: 14%;
  height: 60rem;
  text-align: center;
  border-radius: 9px;
  flex-direction: column;
  align-items: center;
  transition: all .3s;
  display: flex;
}

.meal-plan-window-week:hover {
  transform: scale(1.05);
}

.meal-plan-window__heading {
  color: #fff;
  width: 100%;
  margin-top: -2px;
  margin-bottom: 1rem;
  font-family: cursive;
  font-size: x-large;
  font-weight: 700;
}

.meal-plan-window .sunday {
  border: 8px solid #ff5733;
}

.meal-plan-window .sunday h4 {
  background-color: #ff5733;
}

.meal-plan-window .monday {
  border: 8px solid #ff69b4;
}

.meal-plan-window .monday h4 {
  background-color: #ff69b4;
}

.meal-plan-window .tuesday {
  border: 8px solid #ffc300;
}

.meal-plan-window .tuesday h4 {
  background-color: #ffc300;
}

.meal-plan-window .wednesday {
  border: 8px solid silver;
}

.meal-plan-window .wednesday h4 {
  background-color: silver;
}

